import _ from 'lodash'

import Table from '../../mixins/table'

import AgentSearch from '../../components/agent-search'

import route from './route'

export default {
  name: route.key,

  mixins: [
    Table
  ],

  components: {
    AgentSearch
  },

  data() {
    return {
      action: null,
      list: null,
      player: {
        id: null,
        account_no: null,
        username: '',
        status: 1
      },
      rules: {
        username: [
          {
            required: true,
            message: 'Please input username',
            trigger: 'blur'
          }
        ],
        agent_id: [
          {
            required: true,
            message: 'The agent/sub-agent field is required.',
            trigger: 'blur'
          },
          {
            required: true,
            message: 'The agent/sub-agent field is required.',
            trigger: 'change'
          }
        ],
      },
      showDialog: false,
      isLoading: true,
      isFetching: false,
      isSaving: false,
      route,
    }
  },

  computed: {
    data() {
      let search = this.search.trim().toUpperCase()
      let data = this.list ? this.list.data.filter(d => {
        return !search
          || (
               d.account_no.indexOf(search) > -1
            || d.username.trim().toUpperCase().indexOf(search) > -1
          )
      }) : []

      return this.sort.order ?
        _.orderBy(data, [this.sort.prop], [this.sort.order])
          : data
    },

    dialogTitle() {
      return this.action == 'add' ?
        'Add new player' : 'Edit player'
    }
  },

  mounted() {
    this.fetch()
  },

  methods: {
    handleClose(done) {
      this.action = null
      this.player = {
        id: null,
        account_no: null,
        username: '',
        status: 1
      }

      if (typeof done == 'function') {
        done()
      }
    },

    openDialog(action) {
      this.action = action
      this.showDialog = true
    },

    closeDialog() {
      this.handleClose()
      this.showDialog = false
    },

    async fetch() {
      try {
        this.list = await this.$store.dispatch('player/list')
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
    },

    async remove(id) {
      try {
        await this.$store.dispatch('player/remove', id)

        this.fetch()
        this.$message({
          type: 'success',
          message: 'Delete Successful'
        })
      } catch(e) {
        this.$message({
          type: 'error',
          message: 'Unable to remove.'
        })
      }
    },

    async edit(id) {
      this.isLoading = true

      try {
        this.player = await this.$store.dispatch('player/get', id)
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }

      this.isLoading = false
      this.openDialog('edit')
    },

    async save() {
      if (this.isSaving) return

      this.$refs.form.validate(async valid => {
        if (!valid || this.isSaving) {
          return
        }

        this.isSaving = true

        try {
          delete this.player.account_no
          delete this.player.pa_id

          await this.$store.dispatch('player/save', this.player)
          let message = this.action == 'add' ?
            'Add Successful' : 'Update Successful'

          this.fetch()
          this.closeDialog()
          this.$message({
            type: 'success',
            message
          })
        } catch(e) {
          this.$message({
            type: 'error',
            message: e.message
          })
        }

        this.isSaving = false
      })
    }
  }
}



